import { NextRequest, NextResponse } from "next/server";
import { verifyToken } from "@/lib/auth";

// Public UI routes + API routes
const PUBLIC_ROUTES = [
  "/auth/login",
  "/auth/signup",
  "/auth/google",
  "/auth/google/callback",

  "/api/auth/login",
  "/api/auth/signup",
  "/api/auth/google",
  "/api/auth/google/callback",

  "/api/courses", // GET (list)
  "/api/search/courses",
  "/api/recommendations",
];

export function middleware(request: NextRequest) {
  const { pathname } = request.nextUrl;

  // Debug incoming route
  console.log(" Middleware Hit:", pathname);

  // Check if route is public
  const isPublicRoute = PUBLIC_ROUTES.some((route) =>
    pathname.startsWith(route)
  );

  if (isPublicRoute) {
    console.log(" Public route → allowing:", pathname);
    return NextResponse.next();
  }

  // Get token
  const tokenCookie = request.cookies.get("auth-token");
  const token = tokenCookie?.value;

  console.log(" Cookie object:", tokenCookie);
  console.log(" Extracted token:", token);

  // No token
  if (!token) {
    console.log(" No auth token — redirecting to login");
    if (pathname.startsWith("/api/")) {
      return NextResponse.json(
        { success: false, error: "Unauthorized" },
        { status: 401 }
      );
    }
    return NextResponse.redirect(new URL("/auth/login", request.url));
  }

  // Verify token
  const decoded = verifyToken(token);
  console.log(" Token decoded:", decoded);

  if (!decoded) {
    console.log(" Invalid token — redirecting to login");

    if (pathname.startsWith("/api/")) {
      return NextResponse.json(
        { success: false, error: "Invalid token" },
        { status: 401 }
      );
    }

    return NextResponse.redirect(new URL("/auth/login", request.url));
  }

  // Token is valid — attach headers
  const requestHeaders = new Headers(request.headers);
  requestHeaders.set("x-user-id", decoded.userId || "");
  requestHeaders.set("x-user-email", decoded.email || "");

  console.log(" Token valid. User:", decoded.email);

  return NextResponse.next({
    request: { headers: requestHeaders },
  });
}

export const config = {
  matcher: ["/((?!_next/static|_next/image|favicon.ico|public).*)"],
};
